.icon {
  background-repeat: no-repeat;
  background-position: center;

  &__logo {
    height: 22px;
    width: 64px;
    margin: 13px 16px;
    background-size: contain;
    background-image: url(./../../images/imagesRoot/Logo.svg);
  }

  &__menu {
    width: 16px;
    height: 16px;
    padding: 16px;
    background-image: url(./../../images/imagesRoot/Menu.svg);
    box-shadow: -1px 0 0 0 $shadow-color;

    &--none {
      display: none;
    }
  }

  &__burger {
    display: block;
    background-image: url(./../../images/imagesRoot/Close.svg);
  }

  &__burger-none {
    display: none;
  }

  &__counter {
    display: none;
  }

  &__counter {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    border: 1px solid $white-bg-color;
    background-color: $button-item-color;
    box-sizing: border-box;

    position: relative;
    left: 8px;
    bottom: 8px;

    display: flex;
    align-items: center;
    justify-content: center;

    &--cart {
      right: 10px;
    }

    &--text {
      color: $white-bg-color;
      font-family: MontRegular;
      font-size: 9px;
    }
  }

  &__favourites,
  &__cart {
    height: 16px;
    width: 16px;
  }

  &__Imglink {
    display: flex;
    justify-content: center;
    padding-block: 24px;
    box-shadow: -1px 0 0 0 $shadow-color;
    width: 50%;
  }

  &__activeImgLink {
    color: $nav-activelink-underline;
    border-bottom: 3px solid $nav-activelink-underline;
    padding-block: 24px 21px;
  }

  &__favourites {
    background-image: url(./../../images/imagesRoot/Favourites.svg);
  }

  &__cart {
    background-image: url(./../../images/imagesRoot/ShoppingBag.svg);
  }

  @include on-tablet {
    &__menu {
      display: none;
    }

    &__Imglink {
      padding: 16px;
    }

    &__activeImgLink {
      padding-bottom: 13.6px;
    }
  }

  @include on-desktop {
    &__Imglink {
      padding: 24px;
      box-shadow: -1px 0 0 0 $shadow-color;
    }

    &__activeImgLink {
      color: $nav-activelink-underline;
      border-bottom: 3px solid $nav-activelink-underline;
      padding-bottom: 21.6px;
    }

    &__logo {
      width: 80px;
      height: 28px;
      margin: 18px 24px;
    }
  }
}
