@import './../../../styles/utils/mixins';
@import './../../../styles/utils/vars';
@import './../../../styles/headingStyles';

.cartPage {
  padding-top: calc(24px + $small-nav-height);

  @include on-desktop {
    padding-top: calc(40px + $big-nav-height);
  }

  &__block {
    @include on-desktop {
      @include page-grid;
    }
  }

  &__productsBlock {
    margin-block: 32px;
    display: grid;
    gap: 16px;
    grid-column: 1 / 17;
  }

  &__titleBlock {
    &--title {
      padding-top: 24px;

      @include on-tablet {
        padding: 0;
      }
    }
  }

  &__priceBlock {
    height: 190px;
    width: 100%;
    border: 1px solid $shadow-color;
    border-radius: 16px;
    margin-bottom: 56px;
    padding-inline: 24px;
    box-sizing: border-box;

    display: flex;
    align-items: center;
    flex-direction: column;

    @include on-tablet {
      margin-bottom: 64px;
    }

    @include on-desktop {
      height: 206px;
      margin-block: 32px 0;
      grid-column: 17 / -1;
    }

    &--price {
      padding-top: 24px;
      font-size: 32px;
    }

    &--counter {
      text-align: center;
      width: 100%;
      color: $nav-link-color;
      border-bottom: 1px solid $shadow-color;
      padding-block: 4px 16px;

      @include on-desktop {
        padding-bottom: 25px;
      }
    }

    &--button {
      margin-top: 16px;
      height: 48px;
      width: 100%;
      background-color: $button-item-color;
      color: $white-bg-color;
      border-radius: 48px;
      border: none;

      @include on-desktop {
        margin-top: 24px;
      }
    }
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 160px;
  gap: 16px;

  border: 1px solid $shadow-color;
  border-radius: 16px;
  box-sizing: border-box;
  padding: 16px;

  @include on-tablet {
    flex-direction: row;
  }

  @include on-desktop {
    height: 128px;
  }

  &__top {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;

    &--leftBlock {
      display: flex;
      align-items: center;
      gap: 16px;

      @include on-tablet {
        gap: 24px;
      }
    }

    @include on-tablet {
      justify-content: flex-start;
      gap: 24px;
      width: 150%;
    }
  }

  &__bottom {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include on-tablet {
      justify-content: flex-end;
      gap: 24px;
    }
  }

  &__numBlock {
    display: flex;
    align-items: center;

    &--button {
      width: 32px;
      height: 32px;
      border: 1px solid $arrow-border-color;
      background-color: $white-bg-color;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }

    &--button-unActive {
      opacity: 50%;
      cursor: auto;
    }

    &--price {
      text-align: right;
      min-width: 80px;
    }

    &--quantity {
      min-width: 32px;
      text-align: center;
    }
  }

  &__button {
    padding: 0;
    width: 16px;
    height: 16px;
    border: none;
    background-color: $white-bg-color;
    cursor: pointer;
  }

  &__image {
    height: 80px;
    width: 80px;
    object-fit: contain;
  }

  &__name {
    text-decoration: none;
    color: $text-color;
  }
}
