@import './../../styles/utils/vars';
@import './../../styles/utils/mixins';

.link {
  display: flex;
  align-items: center;
  padding-block: 8px;
  width: min-content;

  text-decoration: none;
  font-family: MontBold, sans-serif;
  font-weight: 800;
  font-size: 12px;
  color: $nav-link-color;
  text-transform: uppercase;
  letter-spacing: 0.04em;

  @include on-tablet {
    padding-block: 18.5px;
  }
}

.activeLink {
  color: $nav-activelink-underline;
  border-bottom: 3px solid $nav-activelink-underline;
  padding-block: 8px 5px;

  @include on-tablet {
    padding-block: 18.5px 15.5px;
  }
}
